<template>
  <div class="container mt-2 mb-2 info-card">
    <div class="row justify-content-md-center" v-if="dn != 0">
      <div class="col col-lg-4 meteostation">
        <h5 class="header bold color-of-institute">Метеостанция ИВМ СО РАН</h5>
        <div class="mb-0">
          <span>{{ dn.windspeed.time }}</span>
        </div>
        <div class="row mt-3">
          <div class="col d-flex col-md-auto align-items-center">
            Температура:
          </div>
          <div class="col col-md-auto temperature bold">
            {{ dn.temperature }}°C
          </div>
        </div>
        <div class="row mt-3"><div class="col">Ветер*:</div></div>
        <div class="row mt-0">
          <div class="col col-lg-4">
            <ul class="list-group">
              <li>скорость:</li>
              <li>порывы:</li>
              <li>направление:</li>
            </ul>
          </div>
          <div class="col-3">
            <ul class="list-group">
              <li v-if="dn.windspeed.s != 0">{{ dn.windspeed.s }} м/с</li>
              <li v-else>Штиль</li>
              <li>{{ dn.windspeed.r }} м/с</li>
              <li v-if="dn.windspeed.s != 0">{{ dn.winddirect[1] }}</li>
              <li v-else>Отсутствует</li>
            </ul>
          </div>
          <div class="col">
            <img
              class="arrow"
              src="../assets/arrow.svg"
              v-bind:style="{ transform: rotate, display: display }"
            />
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="80"
              height="80"
              version="1.1"
            >
              <circle
                cx="40"
                cy="40"
                r="30"
                stroke="#4472c4"
                stroke-width="1"
                fill="none"
              />
              <line
                x1="40"
                y1="0"
                x2="40"
                y2="80"
                style="stroke: #4472c4; stroke-width: 1"
              />
              <line
                x1="0"
                y1="40"
                x2="80"
                y2="40"
                style="stroke: #4472c4; stroke-width: 1"
              />
              <line
                x1="10"
                y1="10"
                x2="70"
                y2="70"
                style="stroke: #4472c4; stroke-width: 1"
              />
              <line
                x1="10"
                y1="70"
                x2="70"
                y2="10"
                style="stroke: #4472c4; stroke-width: 1"
              />
            </svg>
          </div>
        </div>
        <div class="row mt-3">
          <div class="col col-lg-4">Влажность:</div>
          <div class="col">{{ dn.humidity }} %</div>
        </div>
        <div class="row mt-3">
          <div class="col col-lg-4">Давление</div>
          <div class="col">{{ dn.pressure }} мм рт. ст.</div>
        </div>
        <div class="row mt-2">
          <div class="col italic">*средние значения за 20 минут</div>
        </div>
      </div>
      <div class="col forecast">
        <div class="text-right">
          <h5 class="header bold color-of-institute">
            Прогноз погоды в Красноярске
          </h5>
        </div>
        <div clasы="item">
          <span class="header italic color-of-institute bold"
            >Среднесибирское УГМС</span
          >
          <p class="text m-0 p-0" v-for="(t, index) in text.ugms" :key="index">
            <span class="bold">{{ t.time }}: </span>{{ t.value }}
          </p>
        </div>
        <div class="item mt-3">
          <span class="header italic color-of-institute bold"
            >Гидрометцентр России</span
          >
          <p
            class="text m-0 p-0"
            v-for="(t, index) in text.russia"
            :key="index"
          >
            <span class="bold">{{ t.time }}: </span>{{ t.value }}
          </p>
        </div>
        <div class="text-right">
          <a :href="`#${anchor}`" @click="goToBlock">Подробнее</a>
        </div>
      </div>
    </div>
    <div class="row justify-content-md-center" v-else><h5>Нет данных</h5></div>
  </div>
</template>

<script>
export default {
  props: ["dn", "text", "anchor"],
  data() {
    return {
      rotate: `rotate(${
        Number(180) + Number(this.dn.winddirect ? this.dn.winddirect[0] : 0)
      }deg)`,
      display: "visible",
    };
  },
  methods: {
    goToBlock: function (event) {
      event.preventDefault();
      let link = event.target.getAttribute("href");
      document
        .querySelector(link)
        .scrollIntoView({ behavior: "smooth", block: "start" });
    },
  },
};
</script>

<style>
</style>



<div class="attribute">
                <span>Ветер</span>
                
              </div>