<template>
  <div class="container">
    <div class="style-of-institute">
      <h5 class="header bold">Архив данных метеостанции ИВМ СО РАН за 3 дня </h5>
    </div>
    <div class="container m-3" v-for="item in dn" :key="item.name">
      <h6 class="text-center" v-if="item.type != 'winddirectcard'">
        {{ item.name }}
      </h6>
      <div class="row" v-if="item.type == 'winddirectcard'">
        <CardWind :data="item" />
      </div>
      <div class="row" v-else-if="item.type == 'winddirect'">
        <div
          class="col-4"
          v-for="(radarData, index) in item.values"
          :key="index"
        >
          <h6 class="text-center">{{ item.time[index].split(" ")[0] }}</h6>
          <RadarChart
            :data="{
              time: item.time,
              values: radarData,
              type: item.type,
              forecast: false,
            }"
          />
        </div>
      </div>
      <div class="chart" v-else>
        <BarChart :data="item" />
      </div>
    </div>
  </div>
</template>
<script>
import BarChart from "./BarChart";
import RadarChart from "./RadarChart";
import CardWind from "./CardWind";
export default {
  props: ["dn"],
  components: {
    BarChart,
    RadarChart,
    CardWind,
  },
};
</script>

<style>
.style-of-institute {
  color: #4472c4;
  padding: 0px;
  margin: 0px;
  border-top: 3px solid #4472c4;
}
</style>